.catblock {
  height: 280px;
  border-radius: 30px;
  background-color: var(--secondary-color);
  background-image: url(/src/img/img/mobile/cat.png);
  background-size: cover;
  background-position: center;
  margin: 20px 0 10px;

  @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
    background-image: url(/src/img/img/mobile/cat\ 2x.png);
  }
  @media screen and (min-width: 768px) {
    height: 304px;
    border-radius: 60px;
    background-size: 364px 302px;
    background-position: 280px;
    background-repeat: no-repeat;
    position: relative;
    margin: 30px 0 16px;
    background-image: url(/src/img/img/tablet/cat_tablet.png);
    @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
      background-image: url(/src/img/img/tablet/cat_tablet\ 2x.png);
    }
  }
  @media screen and (min-width: 1280px) {
    width: 592px;
    height: 654px;
    background-size: 536px 632px;
    background-position: 22px 22px;
    background-image: url(/src/img/img/desktop/cat_desktop.png);
    @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
      background-image: url(/src/img/img/desktop/cat_desktop\ 2x.png);
    }
    margin: 0;
  }
}
